/*******************************************************************************
 * _multi_column_layout.layout.scss
 * layout
 ******************************************************************************/
.multi-column-layout {
  width: 100%;
  height: 100%;
}

.multi-column-layout .multi-column-layout__region {
  position: relative;
  padding: 1%;
  background: rgba(0, 0, 0, 0.1);
  overflow: hidden;
  z-index: 10;
}

@media (min-width: 36em) {
  .multi-column-layout > div {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
  .multi-column-layout .multi-column-layout__region {
    flex: 1;
  }
}
